.Cards{
  display:grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  gap:30px;
}
.Card{
  background-color:#c7caf8;
  border:1px solid #333;
  border-radius:5px;

}

@media only screen and (max-width:600px){
  .Cards{

    grid-template-columns: repeat(1, 1fr);

    gap:10px;
  }
}